.QuizQuestion {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 2em;
  text-align: center;
}
.QuizEnd {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: row;
      flex-direction: row;
  text-align: center;
  font-size: 2em;
  padding-top: 20px;
}
.QuizEnd a {
  display: block;
  padding-top: 20px;
}
ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.answers ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.answers ul li button {
  padding: 20px 40px;
  background: transparent;
  border: 1px solid #dddddd;
  border-radius: 4px;
  font-size: 22px;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
}

.answers ul li button:hover {
  background: azure;
}

.error {
  color: red;
}

.quiz-category {
  padding: 20px 40px;
  background: transparent;
  border: 1px solid #dddddd;
  border-radius: 4px;
  font-size: 16px;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.quiz-category:hover {
  background: azure;
  text-decoration: none;
}


